import React from 'react'
import { Link, graphql } from 'gatsby'
import Layout from '../components/Layout'
import SEO from '../components/seo'

class DesignIndex extends React.Component {

  render() {
      console.log('this.props', this.props);
    const siteTitle = this.props.data.site.siteMetadata.title
    return (
        <Layout location={this.props.location} title={siteTitle} custom >
          <SEO 
            title={siteTitle}
            keywords={[`blog`, `dwb`, `董文博`, `dwbbb`, `dwbbb.com`,`javascript`, `design`]}
          />
            <div className="m-design-container">
                <div className="m-design-item">
                    <a href="https://www.npmjs.com/package/ydoc-theme-demo" target="_blank" className="href">
                        <img className="img" src="https://ws1.sinaimg.cn/large/006cGJIjly1fwkjg518vqj30850533yn.jpg" srcSet="https://ws1.sinaimg.cn/large/006cGJIjly1fwkjg51sucj30ga0a6my0.jpg 2x" />
                    </a>
                    <h3 className="title">
                        <a href="https://www.npmjs.com/package/ydoc-theme-demo" target="_blank" className="href">demo</a>
                    </h3>
                    <p className="desc m-test">默认主题，以 ‘无形’ 代替 ‘有形’，去除冗余的设计元素</p>
                </div>
            </div>
        </Layout>
    )
  }
}

export default DesignIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
